body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  box-sizing: border-box;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
*{
  font-size: 25px;
  color: white;
  text-align: center;
}
.container{
  width: 700px;
  padding-top: 20px;
  padding-bottom: 20px;
  margin: 0 auto;
  height: auto;
  background-color: #3d353d;
  border-radius: 20px 20px 20px 20px;
}

.header{
  margin: 0 auto;
  border-radius: 20px 20px 0 0;
  display:flex; 
  align-items:center;
  justify-content: center;
  flex-direction: row;

  width: 550px;
  background-color: #F55A5A;
  gap: 0.5em;

}
.logo--travel{
  width: 24px;
  height: 24px;
}
.header--title{
  font-size: 16px;
  color: white;
}
.footer{
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: space-around;
  background-color: #515154;
  ;
  max-width: 550px;
  height: 64px;
  border-radius:0 0 20px 20px;
}
.footer > a:hover{
  cursor: pointer;
}
.travel{
  padding-top: 20px;
  margin: 0 auto;
  display: flex;
  justify-content: space-evenly;
  width: 550px;
  align-items:center;
  background-color:#515154;



}
.travel .travel--img > img{
  border-radius: 5px 5px 5px 5px;
  height: 168px;
  width: 125px;
  object-fit: cover;
}
.travel--content{
  width: 326px;
  height: auto;

}
.travel--location{
  display: flex;
  align-items:center;
  gap: 0.5em;
}
.travel--location >h3, a {
font-size: 10px;
}
.travel--location >h3{
  text-transform: uppercase;
  letter-spacing: 2px;
}

.travel .travel--location > i{
  color:#F55A5A !important;
}
.travel--content .travel--title{
  text-align: start;
  font-size: 25px;
  font-weight: bold;
}
.travel--content .travel--date{
  padding-top: 15px;
  text-align: start;
  font-size: 11px;
  font-weight: bold;
}
.travel--content .travel--content{
  padding-top: 10px;
  padding-bottom: 10px;
  text-align: start;
  font-size: 11px;
  font-weight: 400;
}

